#searches {
    background-color: white;
    /* display: flex; */
    border-radius: 12px;
    -ms-flex-flow: column wrap;
        flex-flow: column wrap;
    height: 95%;    
    margin: 5px;
    padding: 5px;
}

.search {
    -ms-flex: 1 1;
        flex: 1 1;
}

.search h2{
    -ms-flex: 1 1;
        flex: 1 1;
    margin: 5px;
    padding: 5px;
}

.search p{
    -ms-flex: 1 1;
        flex: 1 1;
    margin: 5px;
    padding: 5px;
}

.search-rating{
    margin: 5px;
    padding: 5px;
}

.search-rating-buttons{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
}

.good {
    -ms-flex: 1 1;
        flex: 1 1;
    -webkit-transition-duration: 0.4s;
         -o-transition-duration: 0.4s;
            transition-duration: 0.4s;  
    background-color: #4CAF50;
    border-radius: 12px;
    padding: 5px;
    color: white;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    border: 2px solid #4CAF50;
}

.good:hover {
    background-color: white;
    color: #4CAF50;
}

.bad {
    -ms-flex: 1 1;
        flex: 1 1;
    -webkit-transition-duration: 0.4s;
         -o-transition-duration: 0.4s;
            transition-duration: 0.4s;  
    background-color: #f44336;
    border-radius: 12px;
    padding: 5px;
    color: white;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    border: 2px solid #f44336;
}

.bad:hover {
    background-color: white;
    color: #f44336;
}


